import React, { cloneElement } from 'react';

export default (children, extraProps = {}) =>
  React.Children.map(
    children,
    component =>
      component
        ? cloneElement(component, {
            ...extraProps,
            ...component.props,
            className: `${component.props.className} toolbar-item`,
          })
        : component,
    null,
  );
