import React, { useRef } from 'react';
import PropTypes from 'prop-types';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import FocusGroupProvider from '@elliemae/ds-shared/FocusGroup/FocusGroupManager';
import { mergeRefs } from '@elliemae/ds-utilities/system';
import decorateToolbarChildren from './decorateToolbarChildren';
import Item from './ToolbarItem';
import ToolbarGroup from './ToolbarGroup';
import Divider from './ToolbarDivider';

const blockName = 'toolbar';

const ToolbarContainer = aggregatedClasses('div')(
  blockName,
  null,
  ({ withDepth, alignment, size }) => ({
    'without-depth': !withDepth,
    [alignment]: !!alignment,
    [size]: size,
  }),
);

const DSToolbar = ({
  containerProps = {},
  innerRef,
  withDepth = true,
  alignment = 'right', // left || right
  autoFocusOnMount = true,
  children = [],
  size = 'normal',
  ...otherProps
}) => {
  const containerRef = useRef(null);
  return (
    <FocusGroupProvider
      autoFocusOnMount={autoFocusOnMount}
      getContainer={() => containerRef.current}
      keyBindings={{
        Tab: 'next',
      }}
      orientation="horizontal"
    >
      <ToolbarContainer
        {...containerProps}
        {...otherProps}
        classProps={{ withDepth, alignment, size }}
        innerRef={mergeRefs(innerRef, containerRef)}
      >
        {decorateToolbarChildren(children)}
      </ToolbarContainer>
    </FocusGroupProvider>
  );
};

DSToolbar.propTypes = {
  containerProps: PropTypes.shape({}),
  /** Shows a shadow rear the toolbar */
  withDepth: PropTypes.bool,
  /** Aligns the toolbar to the left or right */
  alignment: PropTypes.oneOf(['right', 'left']),
  /** Toolbar size */
  size: PropTypes.oneOf(['normal', 'compact']),
};

export { Item as ToolbarItem, ToolbarGroup, Divider as ToolbarDivider };

export default DSToolbar;
